body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	font-family: 'Open Sans', sans-serif;
	height: 100%;
	background-color: #333333;
}

.App {
	text-align: center;
	height: 100%;
}

.header-main-div {
	z-index: 1;
	position: sticky;
	top: 0;
	background: #212121;
	margin: 0;
	padding: 10px 5%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	color: #ffffff9e;
	box-shadow: 0px 6px 11px 0px #00000036;
}

.header-heading {
	margin: 0px;
}

.button-div {
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
}

.btn {
	background: transparent;
	box-shadow: -2px -2px 4px 0px #ffffff29, 2px 2px 8px 0px #00000080;
	margin: 5px;
	width: 63px;
	border-style: none;
	border-radius: 10px;
	height: 34px;
	color: #ffffff9e;
	-webkit-border-radius: 10px;
	-moz-border-radius: 10px;
	-ms-border-radius: 10px;
	-o-border-radius: 10px;
}

.btn:disabled {
	box-shadow: inset -2px -2px 4px 0px #ffffff29, inset 2px 2px 8px 0px #00000080;
	color: #ffffff24;
}

.btn:focus {
	outline: none;
	box-shadow: inset -2px -2px 4px 0px #ffffff29, inset 2px 2px 4px 0px #00000080;
}

.card-div {
	background: #333333;
	height: 155px;
	width: 118px;
	border-radius: 10px;
	margin: auto;
	display: flex;
	justify-content: space-between;
	text-align: left;
	padding: 10px;
	font-size: 0.9em;
	flex-direction: column;
	/* Light Theme box-shadow: -8px -8px 11px 0px #ffffffa8, 13px 13px 20px 0px #00000030; */
	box-shadow: -8px -8px 11px 0px #ffffff0a, 13px 13px 20px 0px #00000030;
	color: #ffffff85;

	transition: all 0.3s;
	-webkit-transition: all 0.3s;
	-moz-transition: all 0.3s;
	-ms-transition: all 0.3s;
	-o-transition: all 0.3s;
}

.view-div {
	margin: 30px 2%;

	height: 100%;
	display: grid;
	gap: 0.5rem;
	row-gap: 3rem;
	grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
}

.add-cards-div {
	margin: 30px 2%;
	display: flex;
	flex-direction: column;
}

.input-form {
	height: 370px;
	background: transparent;
	margin: 20px;
	max-width: 360px;
	min-width: 245px;
	align-self: center;
	display: flex;
	padding: 20px;
	flex-direction: column;
	justify-content: space-evenly;
	border-radius: 10px;
	box-shadow: -8px -8px 11px 0px #ffffff0a, 13px 13px 20px 0px #00000030;
	-webkit-border-radius: 10px;
	-moz-border-radius: 10px;
	-ms-border-radius: 10px;
	-o-border-radius: 10px;
}

.input-field {
	background: transparent;
	background-color: transparent;
	border-style: none;
	border-radius: 10px;
	padding: 15px;
	color: #cccccc;
	box-shadow: inset -2px -2px 4px 0px #ffffff29, inset 2px 2px 4px 0px #00000080;
	-webkit-border-radius: 10px;
	-moz-border-radius: 10px;
	-ms-border-radius: 10px;
	-o-border-radius: 10px;
}

.input-field:focus {
	outline: none;
}

.input-field:-internal-autofill-selected {
	-webkit-appearance: menulist-button;
	background-color: rgba(232, 240, 254, 0) !important;
	background-image: none !important;
	color: -internal-light-dark-color(black, white) !important;
}

.input-field::selection {
	background: transparent;
}

.sections {
	background: transparent;
	box-shadow: -2px -2px 4px 0px #ffffff29, 2px 2px 8px 0px #00000080;
	padding: 5px 10px;
	border-style: none;
	border-radius: 4px;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	-ms-border-radius: 4px;
	-o-border-radius: 4px;
	transition: all 0.5s;
	-webkit-transition: all 0.5s;
	-moz-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-o-transition: all 0.5s;
}

.flip-info {
	font-size: 0.6em;
	color: #ffffff40;
	text-align: center;
}

.defaultPStyle {
	margin: 0px;
	padding: 0px;
}
.shrinkP {
	font-weight: 100;
}

.card-name {
	font-weight: 900;
}

.card-price {
	font-weight: 900;
}

.card-category {
	font-weight: 900;
}
